/* General Styles */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  background-color: #000;
  color: #fff;
  overflow-x: hidden;
}

/* Header Styling */
h1 {
  margin: 0;
  padding: 0.5rem 0;
  text-align: center;
  font-size: 2rem;
  font-weight: bold;
}

/* Grid Layout for Week */
.grid-cols-7 {
  display: grid;
  grid-template-columns: repeat(7, minmax(0, 1fr));
  gap: 4px;
  width: 100%;
  padding: 0;
  margin: 0 auto;
  max-width: 100vw;
}

/* Ring Container */
.ring-container {
  margin: 0.2rem;
}

/* Responsive for Small Screens */
@media (max-width: 600px) {
  .grid-cols-7 {
    grid-template-columns: repeat(7, minmax(0, 1fr));
    gap: 4px;
    width: 100%;
  }

  .w-24 {
    width: 4rem;
    height: 4rem;
  }

  .text-xs {
    font-size: 0.65rem;
  }
}

/* Margin Utilities */
.mb-2 {
  margin-bottom: 0.5rem;
}

.mb-6 {
  margin-bottom: 0.5rem;
}

/* Size Utilities */
.w-24 {
  width: 6rem;
  height: 6rem;
}

/* Positioning */
.absolute {
  position: absolute;
}

.relative {
  position: relative;
}

.inset-0 {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

/* Font Styles */
.font-bold {
  font-weight: bold;
}

.text-xs {
  font-size: 0.75rem;
}
